header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1240px;
  width: auto;
  margin: auto;
  align-items: center;
  height: 70px;
  nav {
    ul {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      li {
        padding: 10px 20px;
        list-style: none;
        a {
          text-decoration: none;
        }
      }
      .cta-btn {
        font-family: "Montserrat", sans-serif;
        border: none;
        color: white;
        padding: 10px 15px;
        background: rgb(65, 65, 255);
        font-size: 16px;
        border-radius: 25px;
        cursor: pointer;
        transition: opacity 0.4s;
        transition: box-shadow 0.4s;
        a {
          color: white;
        }

        &:hover {
          opacity: 0.9;
          box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px,
            rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px,
            rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px,
            rgba(0, 0, 0, 0.07) 0px 32px 64px;
        }
      }
    }
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  header {
    max-width: 1024px;
    margin: auto 50px;
    h1 {
      font-size: 30px;
    }
    nav {
      ul {
        a {
          font-size: 14px;
        }
      }
    }
  }
}

@media (min-width: 320px) and (max-width: 767px) {
  header {
    padding-top: 25px;
    flex-direction: column;
    height: 120px;
    margin-bottom: 50px;
    h1 {
      font-size: 24px;
    }
    nav {
      ul {
        flex-wrap: wrap;
        a {
          font-size: 12px;
        }
        .cta-btn {
          padding: 10px 15px;
          font-size: 12px;
        }
      }
    }
  }
}
